import React ,{useState,useRef,useEffect} from 'react'
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import  { Link,useHistory } from 'react-router-dom';
import { Form,Button,Card,Alert } from 'react-bootstrap';
import { useAuth } from '../contexts/AuthContext';

const useStyles = makeStyles((theme: Theme) =>
createStyles({
  login : {
      position:'relative',
      height:'100%',
      background:'url("https://assets.nflxext.com/ffe/siteui/vlv3/757ab38f-5d08-40bc-b3eb-eaba63ed8203/93c34f94-56c8-40a7-8b2e-b4aac6427977/GB-en-20210125-popsignuptwoweeks-perspective_alpha_website_medium.jpg") center no-repeat',
      backgroundSize:'cover'
  },
  logo:{
      position:'fixed',
      left:'0px',
      width:'150px',
      objectFit:'contain',
      paddingLeft:'20px'
  },
  signInbutton:{
      position:'fixed',
      right:'20px',
      top:'20px',
      padding:'10px 20px',
      fontSize:'1 rem',
      color:'#fff',
      backgroundColor:'#e50914',
      fontWeight:'600',
      border:'none',
      cursor:'pointer'
  },
  login_background:{

  },
  gredient:{
     width:'100%',
     zIndex:'1',
     height:'100vh',
     background:'rgba(0,0,0,0.5)'
  },
  body:{
    position:'absolute',
    top:'10%',
    marginLeft:'auto',
    marginRight:'auto',
    textAlign:'center',
    left:'0',
    right:'0',
    zIndex:1,
    color:'#fff',
    padding:'20px',
    width:'fit-content'
  },
  signupform:{
      maxWidth:'550px',
      padding:'50px',
      marginLeft:'auto',
      marginRight:'auto',
      background:'rgba(0,0,0,.85)'
      },
  input:{
     outlineWidth:0,
     height:'40px',
     marginBottom:'14px',
     marginTop:'30px',
     borderRadius:'5px',
     border:'none',
     padding:'5px 15px',
     color:'gray'
  },
  button:{
     padding:'16px 20px',
     fontSize:'1rem',
     color:'#fff',
     borderRadius:'5px',
     backgroundColor:'#e50914',
     fontWeight:'600',
     border:'none',
     cursor:'pointer',
     marginTop:'20px'
  },
  h4:{
      textAlign:'left',
      color:'#737373',
    fontSize: '16px',
    fontWeight: '500',
    marginTop: '10px'
  },
  h41:{
      textAlign:'right',
      color:'#737373',
    fontSize: '16px',
    fontWeight: '500',
    marginTop: '26px'
  },
  h7:{textAlign:'left',
    marginTop: '11px',
    fontSize: '13px',
    color: '#8c8c8c'
  }
}));

export default function EditProfile() {
const classStyle = useStyles();
   const history = useHistory();
   const [email,setemail] = useState();
   const emailRef=useRef();
   const passwordRef = useRef();
   const confirmpasswordRef = useRef();
   const [error,setError] =useState('');
   const [loading ,setLoading]=useState(false);
   const [message,setMessage]=useState('');
   const { user,updateEmail,updatePassword } =useAuth();

  function handleSubmit(e){
      e.preventDefault();
      if(passwordRef.current.value !== confirmpasswordRef.current.value)
      {
        return setError("password do not match");
      }
      const promises=[];

      if(emailRef.current.value!= email)
          promises.push(updateEmail(emailRef.current.value));
      if(passwordRef.current.value && confirmpasswordRef.current.value)
          promises.push(updatePassword(passwordRef.current.value));

      Promise.all(promises).then(()=>{
        history.push("/profile");
        }).catch(()=>{
          setError("Failed to update the account");
        }).finally(()=>{
        setLoading(false);
        })
        localStorage.removeItem('email');
   }

   function homescreen(){
        history.push("/homescreen");
   }

   useEffect(()=>{
           emailRef.current.value = JSON.parse(localStorage.getItem('email'));
           setemail(JSON.parse(localStorage.getItem('email')));
   },[]);
    return (
        <div className={classStyle.login}>
            <div className={classStyle.login_background}>
               <img onClick={homescreen}
               className={classStyle.logo}
               src="https://assets.stickpng.com/images/580b57fcd9996e24bc43c529.png"/>
            </div>
            <div className={classStyle.gredient}></div>
            <div  className={classStyle.body}>
                    <div className={classStyle.signupform}>
                    <form style={{display:'grid',flexDirection:'column'}} onSubmit={handleSubmit}>
                    <h1 style={{marginBottom:'40px',textAlign:'left'}}>Update Email/Password</h1>
                     {error && <Alert variant="danger">{error}</Alert>}
                      {message && <Alert variant="success">{message}</Alert>}
                    <input className={classStyle.input} ref={emailRef} type="email" placeholder="Name@Example.com" required />
                    <input className={classStyle.input} ref={passwordRef} type="password" placeholder="Enter the new password to update"  />
                    <input className={classStyle.input} ref={confirmpasswordRef} type="password" placeholder="confirm the new password"  />
                    <button disabled= {loading} className={classStyle.button} type="submit">Update Details</button>
                    </form>
                    </div>
           </div>
        </div>
    )
}
