import React ,{useState,useEffect} from 'react'
import Category from './category/category';
import request from '../api-request-url';
import {Alert} from 'react-bootstrap';
import MainPoster from './mainPoster/mainPoster';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import { useAuth } from '../contexts/AuthContext';
import  { Link,useHistory } from 'react-router-dom';
import {store} from '../redux/_helpers/store'
import {logout } from '../redux/Actions/user.actions'
import PlansScreen from '../components/PlansScreen'

const useStyles = makeStyles((theme: Theme) =>
createStyles({
  logo:{
      position:'fixed',
      left:'0px',
      width:'150px',
      objectFit:'contain',
      paddingLeft:'20px'
  },
  profilescreen:{
       height:"100vh",
       color:'white'
  },
  profilebutton:{
      position:'fixed',
      right:'20px',
      top:'20px',
      padding:'10px 20px',
      fontSize:'1 rem',
      color:'#fff',
      backgroundColor:'#e50914',
      fontWeight:'600',
      border:'none',
      cursor:'pointer',
      backgroundSize:'cover',
      backgroundPosition:'center center',
      backgroundImage:'url("https://pbs.twimg.com/profile_images/1240119990411550720/hBEe3tdn_400x400.png")',
      height:'40px',
      width:'40px'
  },
  body:{
      display:'flex',
      flexDirection:'column',
      width:'50%',
      marginLeft:'auto',
      marginRight:'auto',
      paddingTop:'5%',
      maxWidth:'700px'
  },
  info:{
      display:'flex'
  },
  h2:{
      textAlign:'left',
      fontSize:'60px',
      fontWeight:'400',
      borderBottom:'1px solid #282c2d',
      marginBottom:'20px'
  },
  details:{
      color:'white',
      marginLeft:'25px',
      flex:'1',
  },
  h21:{
      textAlign:'left',
      backgroundColor:'gray',
      padding:'15px',
      fontSize:'15px',
      paddingLeft:'20px'
  },
  signout:{
      padding:'10px 20px',
      fontSize:'1rem',
      marginTop:'5%',
      width:'100%',
      color:'#fff',
      backgroundColor:'#e50914',
      border:'none',
      cursor:'pointer',
      fontWeight:'600'
  },
  plans:{
      marginTop:'20px',
      backgroundColor:'black'
  },
  h3:{
      textAlign:'left',
      borderBottom:'1px solid #282c2d',
      paddingBottom:'10px'
  }
}));
export default function ProfileScreen() {
      const {Logout, user } =useAuth();
      const history = useHistory();
      const [error,setError] =useState('');
      const classStyle = useStyles();
    async function handleLogout(){
         try{
                setError("");
                await Logout();
                history.push("/login")
                store.dispatch(logout());
            }catch(error){
                setError(error.message);
            }
    }
     function Profilescreen(e){
         e.preventDefault();
         history.push('/homescreen');
     }

     useEffect(()=>{
          if(user)localStorage.setItem('email', JSON.stringify(user.email));
     },[])
    return (
        <>
        <div className={classStyle.profilescreen}>
            <img onClick={Profilescreen}
               className={classStyle.logo}
               src="https://assets.stickpng.com/images/580b57fcd9996e24bc43c529.png"/>
       
            <button  className={classStyle.profilebutton}></button>
            <div className={classStyle.body}>
                    <h2 className={classStyle.h2}>Edit Profile</h2>
                    <div className={classStyle.info}>
                    <img style={{height:'100px'}} src="https://pbs.twimg.com/profile_images/1240119990411550720/hBEe3tdn_400x400.png" alt=""/>
                        <div className={classStyle.details}>
                            { error && <Alert variant="danger">{error}</Alert> }
                            <h2 className={classStyle.h21}>{user && user.email} <Link to='/edit-profile' style={{marginLeft:'400px'}}>Edit</Link></h2>
                            <div className={classStyle.plans}>
                               <h3 className={classStyle.h3}>Plans</h3>
                                <PlansScreen/>
                               <button onClick={handleLogout} className={classStyle.signout}>Sign Out</button>
                            </div>
                        </div>
                    </div>
             </div>
       </div>
        </>
    )
}
