import React from 'react'
import Category from './category/category';
import request from '../api-request-url';
import MainPoster from './mainPoster/mainPoster';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import  { Link,useHistory } from 'react-router-dom';
const useStyles = makeStyles((theme: Theme) =>
createStyles({
  logo:{
      position:'fixed',
      left:'0px',
      width:'150px',
      objectFit:'contain',
      paddingLeft:'20px'
  },
  login_background:{

  },
  profilebutton:{
      position:'fixed',
      right:'20px',
      top:'20px',
      padding:'10px 20px',
      fontSize:'1 rem',
      color:'#fff',
      backgroundColor:'#e50914',
      fontWeight:'600',
      border:'none',
      cursor:'pointer',
      backgroundSize:'cover',
      backgroundPosition:'center center',
      backgroundImage:'url("https://pbs.twimg.com/profile_images/1240119990411550720/hBEe3tdn_400x400.png")',
      height:'40px',
      width:'40px'
  },
  
}));
export default function Homescreen() {
   const classStyle = useStyles();
   const history = useHistory();

   function profile(e){
         e.preventDefault();
         history.push("/profile");
   }
    return (
        <>
        <div className={classStyle.login_background}>
               <img 
               className={classStyle.logo}
               src="https://assets.stickpng.com/images/580b57fcd9996e24bc43c529.png"/>
            </div>
            <button  onClick ={profile} className={classStyle.profilebutton}></button>
      <MainPoster></MainPoster>
      <Category title={"NETFLIX ORIGINALS"} resourceURL={request.NetFlixOriginals} realPoster></Category>
      <Category title={"Trending Now"} resourceURL={request.Trending}></Category>
      <Category title={"Action Movies"} resourceURL={request.ActionMovies}></Category>
      <Category title={"Comedy Movies"} resourceURL={request.ComedyMovies}></Category>
      <Category title={"Horror Movies"} resourceURL={request.HorrorMovies}></Category>
      <Category title={"Romance Movies"} resourceURL={request.RomanceMovies}></Category>
      <Category title={"Documentaries"} resourceURL={request.Documentaries}></Category>
        </>
    )
}
