import React, { useEffect,useState } from "react";
import api from '../../axios';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import YouTube from 'react-youtube';
import movieTrailer from 'movie-trailer';

const image_base_URL = 'https://image.tmdb.org/t/p/original';

const useStyles = makeStyles((theme: Theme) =>
createStyles({
  title:{
        color    : 'white',
        fontFmily: 'Verdana, Arial, Helvetica, sans-serif',
        display  : 'flex',
        fontSize : '30px'
  },
Poster : {
     objectFit  : 'contain',
         width  : '100%',
     maxHeight  : '150px',
     transition :`transform 450ms`,
     marginRight:'10px',
     '&:hover' : {
         transform:'scale(1.08)'
     }
},

Posters : {
      display    : 'flex',
      overflowY  : 'hidden',
      overflowX  : 'scroll',
        padding  :  '20px',
      '&::-webkit-scrollbar': {
        display: 'none'
      }
},
largeSizePoster : {
       maxHeight:'250px'
}
}));

 function Category(props) {
const classStyle = useStyles();
  const [movies,setMovies] = useState([]);
  const [trailer,setTrailer] = useState("");

  useEffect(() => {
        async function fetchMovies(){
          const response=await api.get(props.resourceURL);
          console.log("json>>",response.data.results);
          setMovies(response.data.results);
        }
        fetchMovies();
  }, [props.resourceURL])

   function getTrailerLink(movie){
     if(trailer){
       setTrailer("");
     }
          movieTrailer(movie.name||"",{ tmdbId: movie.id }).then((trailerURL)=>{
           const params = new URLSearchParams(new URL(trailerURL).search);
           console.log("params>>",params.get("v"));
           setTrailer(params.get("v"));

         }).catch(error =>{

           console.log("error>>>>",error);
     });
  }
const opts = {
      height: '390',
      width: '100%',
      playerVars: {
        autoplay: 1,
      },
    };
    return(
      
       <div className="Category">
           <h1 className = {classStyle.title}>{props.title}</h1>
           <div className={classStyle.Posters}>
                  {
                  movies.map(movie=>(
                    <img className={`${classStyle.Poster} ${ props.realPoster && classStyle.largeSizePoster }`} 
                    key={movie.id} src={`${image_base_URL}${props.realPoster?movie.poster_path:movie.backdrop_path}`} 
                    onClick = {()=>getTrailerLink(movie) }alt = {movie.name}></img>
                  ))}
           </div>
           {trailer && 
           <YouTube videoId = { trailer } opts={opts}/>
           }
       </div>);
  }

export default Category;
